/* @import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap'); */

.preload-transitions * {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -ms-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}

html {
  font-size: 62.5%;
  font-family: "Josefin Sans", sans-serif;
  color: #494c6b;
}

[data-theme="darkTheme"] {
  color: #c8cbe7;
}

body {
  font-size: 1.8rem;
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
  pointer-events: none;
}

.moon {
  font-size: 4rem;
}

.btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  color: #9495a5;
  font-size: 1.4rem;
  line-height: 1.4rem;
  font-family: inherit;
}

.filter {
  font-size: 1.4rem;
  color: #a4a5b1;
  font-weight: bold;
  cursor: pointer;
  transition: all 2000ms ease-in-out;
}
/* .filter:hover{
    color: #E3E4F1;
} */

.picture {
  background-image: url("../public/bg-desktop-light.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 400px;
  z-index: -1;
}

[data-theme="darkTheme"] .picture {
  background-image: url("../public/bg-desktop-dark.jpg");
}

.active {
  color: #3a7cfd;
}

.btn--bold {
  font-weight: bold;
}

.btn:focus {
  outline: none;
}

.btn:hover {
  color: #494c6b;
  /* color: #494C6B; */
}

[data-theme="darkTheme"] .btn:hover {
  color: #e3e4f1;
}

p.btn {
  pointer-events: none;
}

p.btn:hover {
  color: inherit;
}

.main__container {
  width: 100vw;
  min-height: 100vh;
  /* background: url("../public/img/bg-desktop-light.jpg"); */
  background-color: #fafafa;
  background-repeat: no-repeat;
  background-size: contain;
  /* background-size: 100vw auto ; */
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

[data-theme="darkTheme"] .main__container {
  /* background: url("../public/img/bg-desktop-dark.jpg"); */
  background-color: #171823;
  background-repeat: no-repeat;
  background-size: contain;
}

.todo {
  position: relative;
  z-index: 1;
  max-width: 540px;
}

.flex {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 7rem 0 0 0;
}

.logo__title {
  color: white;
  font-weight: bold;
  font-size: 4rem;
  line-height: 4rem;
  letter-spacing: 15px;
}

.btn--theme {
  background-color: transparent;
  border: none;
}

.todo__header {
  width: 490px;
  background-color: white;
  padding: 2rem 2.4rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  margin-top: 4.8rem;
  margin-bottom: 2.4rem;
  border: 1px solid #e3e4f1;
}

.todo__header label {
  position: absolute;
  left: -25vw;
}

[data-theme="darkTheme"] .todo__header {
  background-color: #25273d;
  border: 1px solid #393a4b;
}

.todo__circle,
.todo__check {
  width: 24px;
  height: 24px;
  border: 1px solid #e3e4f1;
  border-radius: 50%;
  background: transparent;
}

[data-theme="darkTheme"] .todo__circle,
[data-theme="darkTheme"] .todo__check {
  border: 1px solid #393a4b;
}

.todo__input,
todo__input::-webkit-input-placeholder {
  font-size: 1.8rem;
  border: none;
  font-family: inherit;
  line-height: 2rem;
  flex: 1 0;
  margin-left: 2rem;
  background: transparent;
}

[data-theme="darkTheme"] .todo__input {
  color: inherit;
}

.todo__input:focus {
  outline: none;
}

.todo__list {
  width: 100%;
  border-top: 1px solid #e3e4f1;
  border-left: 1px solid #e3e4f1;
  border-right: 1px solid #e3e4f1;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.todo__list:first-child {
  overflow: hidden;
}

[data-theme="darkTheme"] .todo__list {
  border-top: 1px solid #393a4b;
  border-left: 1px solid #393a4b;
  border-right: 1px solid #393a4b;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.todo__elem {
  max-width: 600px;

  display: flex;
  align-items: center;
  background-color: white;
  border-bottom: 1px solid #e3e4f1;
  padding: 2rem 2.4rem;
  justify-content: space-between;
}
ul {
  list-style: none;
  margin-bottom: 0;
  padding: 0;
}

[data-theme="darkTheme"] .todo__elem {
  border-bottom: 1px solid #393a4b;
}

[data-theme="darkTheme"] .todo__elem {
  background-color: #25273d;
}

.todo__elem p {
  flex: 1;
  margin-left: 2rem;
}

.todo__elem--hide {
  display: none;
}

.todo__elem--checked p {
  color: #d1d2da;
  text-decoration: line-through;
}

[data-theme="darkTheme"] .todo__elem--checked p {
  color: #4d5067;
}

.todo__check {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: white; */
  transition: border 0.5s ease-in-out;
}

[data-theme="darkTheme"] .todo__check img {
  transform: scale(0);
  transition: transform 0.5s ease-in-out;
}

.todo__elem--checked .todo__check {
  background: linear-gradient(to right, #55ddff, #c058f3);
}

[data-theme="darkTheme"] .todo__elem--checked .todo__check img {
  transform: scale(1);
}

.todo__elem:hover > .todo__check {
  /* background: linear-gradient(to right, #55DDFF, #C058F3);  */
  border: 1px solid #c058f3;
}

.todo__delete {
  font-size: 2rem;
  background: transparent;
  border: none;
  transform: scale(0);
  opacity: 0;
  transition: transform 400ms ease, opacity 400ms ease;
}
[data-theme="darkTheme"] .todo__delete {
  color: #979797;
}

.todo__elem:hover > .todo__delete {
  transform: scale(1);
  opacity: 1;
}

.todo__filters__sides {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background: white;
  border-bottom: 1px solid #e3e4f1;
  border-left: 1px solid #e3e4f1;
  border-right: 1px solid #e3e4f1;

  padding: 2rem 0rem;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
[data-theme="darkTheme"] .todo__filters__sides {
  background: #25273d;
  border-bottom: 1px solid #393a4b;
  border-left: 1px solid #393a4b;
  border-right: 1px solid #393a4b;
}

.todo__filters__center {
  background: white;
  padding: 1.9rem 2.4rem;
  gap: 1.5rem;
  /* border-bottom: 1px solid  #e3e4f1; */
  border-radius: 5px;
  display: flex;
}

[data-theme="darkTheme"] .todo__filters__center {
  background: #25273d;
  border-bottom: 1px solid #393a4b;
}

.todo__filters__center label {
  background: transparent;
  margin: 0 0.8rem;
}

.todo__filters__center input[type="radio"] {
  display: none;
}

.todo__filters__center input[type="radio"]:checked + label {
  color: #3a7cfd;
}

footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem 0;
  display: flex;
  justify-content: center;
}

[data-theme="darkTheme"] footer a {
  color: inherit;
  font-weight: bold;
}

@media (max-width: 738px) {
  html {
    font-size: 47%;
  }
  .main__container {
    padding: 0 15%;
    /* background: url("../public/img/bg-mobile-light.jpg"); */
    background-color: #fafafa;
    background-repeat: no-repeat;
    background-size: contain;
    /* background-size: 100vw auto ; */
    /* display: flex;
     flex-direction: column;
     align-items: center;
     position: relative; */
  }

  [data-theme="darkTheme"] .main__container {
    /* background: url("../public/img/bg-mobile-dark.jpg"); */
    background-color: #171823;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .todo {
    width: 100%;
    min-width: 327px;
  }

  .todo__filters__center {
    position: static;
    display: block;
    transform: none;
    margin-top: 1.5rem;
    text-align: center;
    border: 1px solid #e3e4f1;
    /* width: 100%; */
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }

  [data-theme="darkTheme"] .todo__filters__center {
    background: #25273d;
    border: 1px solid #393a4b;
  }
  footer {
    /* position: static;  */
    margin: 2rem 0;
    text-align: center;
    line-height: 3rem;
  }
}
